<template>
    <div class="container" @click="handleGallaryClick">
        <div class="wrapper">
            <swiper :options="swiperOption" >
                <swiper-slide v-for="(pic,index) of imgs" :key="index">
                    <img class="gallary-img" :src="pic">
                </swiper-slide>
                <div class="swiper-pagination" slot="pagination"></div>
            </swiper>
        </div>
    </div>
</template>

<script>
export default {
  name: 'CommonGallary',
  props: {
    imgs: {
      type: Array,
      default () {
        return []
      }
    }
  },
  data () {
    return {
      swiperOption: {
        pagination: '.swiper-pagination',
        paginationType: 'fraction',
        observeParents: true,
        observer: true
      }
    }
  },
  methods: {
    handleGallaryClick () {
      this.$emit('close')
    }
  }
}
</script>

<style lang="stylus" scoped>
    .container >>> .swiper-container {
        overflow: inherit;
    }
    .container {
        display: flex;
        flex-direction: column;
        justify-content: center;

        position: fixed;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        background-color: #000;
        z-index: 2;
    }
    .wrapper {
        height: 0;
        width: 100%;
        padding-bottom: 100%;
        overflow: inherit;
    }
    .gallary-img {
        width: 100%;
        /*margin-top: .8rem;*/
    }
    .swiper-pagination {
        color: #fff;
        bottom: -1.8rem;
    }
</style>



